section.experience {
    display: block;
    height: fit-content;
}
.experienceContentWrapper {
    width: 100%;
    height: fit-content;
    background-color: #B09B71;
    border-radius: 8px;
}
.experienceContentWrapper div {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
}
.experienceIcon {
    padding: 1.5rem 4rem;
    margin-top: 1rem;
}
.experienceIcon img {
    width: 5rem;
    height: 5rem;
}
.experienceContentWrapper .experiencesAndTime {

    padding: .8rem;
}
.experienceContentWrapper .experiencesAndTime * {
    display: inline-block;
    width: 100%;
}
.experiencesAndTimeWrapper h2 {
    font-size: 1em;
    padding: 0;
}
.experiencesAndTimeWrapper span {
    color: #000000;
    font-size: .5em;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
}
.experiencesAndTimeWrapper p {
    color: #FFF9D7;
}