.about {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.myAvatarContainer {
    background-color: #87805E;
    height: 15rem;
    border-radius: 50%;
    bottom: 0;
    margin-bottom: 7rem;
    padding: 1rem;
    align-self: center;
}
.myAvatarContainer img {
    padding: 1rem 2rem 1rem 1rem;
    width: 14rem;
}
.aboutMeContainer {
    width: 60%;
}
.aboutMeContainer h1 {
    text-align: right;
    color: #87805E;
    font-weight: bolder;
}
.aboutMeContainer p {
    text-align: justify;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}