section.project {
    padding: 8rem 4rem;
}
.projectsContainer h1 {
    text-align: center;
}
.projectsContainer img {
    width: 4rem;
}
.projectsWrapper {
    display: flex;
    justify-content: space-between;
    text-align: center;
    width: 100%;
}
.projectsWrapper div {
    margin-right: .8rem;
    border-radius: 8px;
    padding: .8rem;
    display: flex;
    flex-direction: column;
    background-color: #B09B71;
}
.project2 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.projectsWrapper h2 {
    font-size: 1em;
}
.projectsWrapper .imageAndExplanationOfProjects {
    padding: 0;
    margin: 0;
    border: none;
    width: fit-content;
    height: min-content;
    border-radius: 0rem;
    clear: both;
    align-items: center;
}
.projectsWrapper .imageAndExplanationOfProjects img:nth-child(1) {
    margin: 0;
    padding: 0 1rem;
    text-align: center;
    width: 100%;
}
.imageAndExplanationOfProjects .projectImage {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    border: none;
    left: 0;
    position: relative;
}
.imageAndExplanationOfProjects {
    position: relative;
    margin: 0;
    display: table;

}
.projectsWrapper .usedLanguages {
    background-color: #19031a5f;
    opacity: 0;
    text-align: left;
    left: 1rem;
    border-radius: 0rem;
    width: 50%;
    position: absolute;
    padding: .8rem;
    bottom: 0;
    top: 0;
}
.projectsWrapper p {
    line-height: 0rem ;
    vertical-align: middle;
    color: #FFF9D7;
    font-size: .5em;
}
.projectsWrapper div a {
    align-items: center;
    text-align: center;
    font-size: .8em;
    display: block;
    margin-top: .8rem;
    color: #FFF9D7;
    background-color: #0000008a;
    border: #87805E 2px solid;
    border-radius: 8px;
    padding: .2rem;
}
.projectsWrapper div a:hover {
    background-color: #FFF9D7;
    color: #87805E;
}
.viewAllProjectsButton {
    float: right;
    cursor: pointer;
    margin: 2rem 0;
    font-size: .8em;
    padding: .5rem;
    border-radius: 4rem;
    color: #FFF9D7;
    background-color: #B09B71;
    border: #87805E 2px solid;
}
button.viewAllProjectsButton:hover {
    background-color: #FFF9D7;
    color: #87805E;
}
.viewAllProjectsButton span {
    color: #FFF9D7;
    background-color: #87805E;
    border-radius: 50%;
    padding: .2rem .4rem;
}