section.certificates {
    display: block;
    height: max-content;
}
.certificationsContainer {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.certificatesIcon {
    background-color: none;
    padding: 0 5.5rem 0 0;
    margin-right: 4rem;
}
.certificatesIcon * {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}
.certificatesIcon img {
    width: 14rem;
    margin-bottom: -2rem;
}
.certifContents {
    display: flex;
    flex-direction: column;
}
.certifContents div {
    background-color: #B09B71;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: row-reverse;
    text-align: left;
    align-items: flex-start;
    justify-content: space-around;
    padding: .8rem;
    border-radius: 8px;
}
.certifContents div:first-child {
    justify-content: space-between;
}
.certifContents img {
    width: 6rem;
    padding: .4rem;
    transition: transform 2s;
}
.certifContents img:hover {
    -webkit-transform: scale(2,2);
    transform: scale(2,2);
}
.certifContents div h2{
    padding-bottom: 1rem;
}
.certifContents div p {
    color: #FFF9D7;
}