.services h1 {
    text-align: right;
}
.services h1 img {
    width: 4rem;
}
.services h4 {
    font-size: .7em;
    color: #000000;
    font-family: 'Indie Flower', cursive;
    padding: .8rem;
}
.servicesContainer {
    text-align: center;
    color: #FFF9D7;
    display: flex;
    justify-content: space-between;
    align-items: stretch, flex-end;
    align-content: flex-end;
}
.servicesContainer img {
    -webkit-transition: transform 1s;
    transition: transform 1s;
    margin: 0 auto;
    width: 8rem;
}
.servicesContainer img:hover {
    -webkit-transform: scale(-.5,.5);
    transform: scale(-.5,.5);
}
.servicesContainer div {
    margin: 0 .2rem;
    align-items: flex-end;
    bottom: 0;
    border-radius: .4rem;
    background-color: #B09B71;
    padding: 1.4rem;
}
.servicesContainer .serviceOne,
.servicesContainer .serviceTwo,
.servicesContainer .serviceFour {
    display: grid;
}
.servicesContainer div p {
    font-family: Arial, Helvetica, sans-serif;
    color: #FFF9D7;
    font-weight: bolder;
    margin-top: 1rem;
}