.upToTopArrow {
    float: right;
    cursor: pointer;
    font-size: 1em;
    position: -webkit-sticky;
    position: sticky;
    top: 88%;
    margin-right: 1rem;
    z-index: 98;
    border-radius: 50%;
    z-index: 9999;
    text-align: center;
    border: .5px solid #87805E;
    padding: .7rem .7rem 0rem;
    font-weight: bolder;
    display: none;
}
.upToTopArrow a {
    padding: 4px 4px -2px;
    color: #87805E;
}

.upToTopArrow:hover,
.upToTopArrow a:hover {
    color: #FFF9D7;
    background-color: #B09B71;
}