@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');
/* FOR ALL */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style-type: none;
    color: #19031a;
}
html {
    scroll-behavior: smooth;
}
body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: #FFF9D7;
    font-size: 2em;
}
.bodyContainer section {
    padding: 8rem;
    width: 100%;
}
h1 {
    font-family: Arial, Helvetica, sans-serif;
    color: #87805E;
    padding-bottom: 3rem;
}
h2{
    color: #87805E;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.2em;
    padding-bottom: 1rem;
}
p,
span {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: .75em;
    line-height: 1.7rem;
}
/* change Language effects */
body.chinese .navigationButtonAndContent button::before {
    top: -2.4rem;
}
/* dark THEME for all */
body.dark  {
    background-color: #000000;
}
body.dark .myImage img {
    content: url('./images/profileImage/myPictureDark.png');
    width: 300px;
    height: 280px;
}
body.dark .backgroundSquere,
body.dark .myAvatarContainer,
body.dark .clockContainer {
    background-color: #1A4D2E;
}
body.dark hr {
    background-color: #382933;
    height: 0.4rem;
}
body.dark .upToTopArrow {
    border: 0.5px solid #1A4D2E;
}
body.dark .upToTopArrow a {
    color: #1A4D2E;
}

body.dark .upToTopArrow:hover,
body.dark .upToTopArrow a:hover {
    color: #AFAEAE;
    background-color: #1A4D2E;
}

body.dark .skillsContentWrapper {
    border: 10px solid #1A4D2E;
}
body.dark .skillsContainer h1 img {
    content: url('./images/icons/skillsDark.png');
    width: 5rem;
    height: 5rem;
}
body.dark .certificatesIcon img {
    content: url('./images/icons/certificateAwardsDark.png');
}
body.dark .experienceIcon img {
    content: url('./images/icons/workExperienceDark.png');
}
body.dark .projectsContainer h1 img {
    content: url('./images/icons/projectsDark.png');
}
body.dark .services h1 img {
    content: url('./images/icons/servicesDark.png');
    width: 4rem !important;
}
body.dark .contactMeIcon img {
    content: url('./images/icons/phoneIconDark.png');
}
body.dark .navigationWrapper .navigationContentWrapper ul li {
    background-color: #000000;
    color: #AFAEAE;
    border: none;
}
body.dark .navigationContentWrapper ul li a {
    color: #AFAEAE;
}
body.dark .navigationWrapper .navigationContentWrapper ul li:hover {
    background-color: #382933;
}
body.dark .navigationButtonAndContent button {
    background-color: #1A4D2E;
}
body.dark .chineseEnglish ,
body.dark .navigationButtonAndContent button {
    color: #AFAEAE;
}
body.dark .logoContainer img {
    content: url('./images/logo/logoDark.png');
}
body.dark.chinese .logoContainer img {
    content: url('./images/logo/logoDarkChinese.png');
}
body.dark h1,
body.dark h2,
body.dark h4,
body.dark h5,
body.dark p,
body.dark a *,
body.dark span {
    color: #AFAEAE;
}
body.dark .myContactAndCVcontainer a {
    background-color: #AFAEAE;
    border: #382933 solid 4px;
    color: #000000;
}
body.dark .myContactAndCVcontainer a:hover {
    color: #AFAEAE;
    background-color: #382933;
    border: #AFAEAE solid 4px;
}
body.dark .myContactAndCVcontainer a:first-child {
    background-color: #382933;
    border: #AFAEAE solid 4px;
    color: #AFAEAE;
}
body.dark .myContactAndCVcontainer a:first-child:hover {
    border: #382933 solid 4px;
    background-color: #AFAEAE;
    color: #000000;
}
body.dark .certifContents div,
body.dark .experienceContentWrapper div,
body.dark .projectsWrapper div,
body.dark .servicesContainer div {
    background-color: #382933;
}
body.dark .certifContents div p,
body.dark .experiencesAndTimeWrapper p,
body.dark .servicesContainer div p {
    color: #AFAEAE;
}
body.dark .services h4 {
    color: #AFAEAE;
}
body.dark .experiencesAndTimeWrapper span {
    color: #19031a;
}
body.dark .projectsWrapper .usedLanguages {
    background-color: #19031a5f;
}
body.dark .projectsWrapper div a {
    border: #AFAEAE 1px solid;
    color: #AFAEAE;
}
body.dark .projectsWrapper div a:hover {
    background-color: #000000;
    color: #AFAEAE;
}
body.dark .viewAllProjectsButton {
    background-color: #382933;
    border: #AFAEAE solid 4px;
    color: #AFAEAE;
}
body.dark .viewAllProjectsButton:hover {
    border: #382933 solid 4px;
    background-color: #AFAEAE;
    color: #000000;
}
body.dark .viewAllProjectsButton span {
    background-color: #382933;
}
body.dark .reviewsWrapper div p {
    color: #19031a;
}
body.dark .contactMeWrapper label,
body.dark .contactMeWrapper textarea,
body.dark .contactMeWrapper input {
    background-color: #000000;
    color: #AFAEAE;
    transform: translateY(0.7rem) translateX(1rem);
}
body.dark .reviewsWrapper div h5 {
    color: #1A4D2E;
}
body.dark .contactMeWrapper input.sendMessageButton {
    color: #AFAEAE;
}
body.dark .contactMeWrapper input.sendMessageButton:hover {
    color: #000000;
    background-color: #AFAEAE;
    border: 4px solid #1A4D2E;
}
body.dark .reviewsContainer a ,
body.dark .reviewsWrapper div {
    background-color: #382933;
}
body.dark .reviewsContainer {
    box-shadow: #382933 0px 0px 11px 0px;
}
body.dark ul.reviewsLength li.currentDot {
    color: #AFAEAE;
}
body.dark ul.reviewsLength li {
    color: #1A4D2E;
}
body.dark .contactMeWrapper input,
body.dark .contactMeWrapper textarea {
    border: #382933 4px solid;
    outline: none;
}
body.dark .contactsIcons .linkedin{
    content: url('./images/icons/linkedinIconDark.png');
}
body.dark .contactsIcons .github{
    content: url('./images/icons/githubIconDark.png');
}
body.dark .contactsIcons .email{
    content: url('./images/icons/mailIconDark.png');
}

/* <!-- 🌐 RESPONSIVE --> */
@media only screen and (max-width: 1450px) {
    section.projects,
    section.services {
        height: max-content;
    }
    .projectsWrapper,
    .servicesContainer {
        display: grid;
        justify-content: center;
    }
    .projectsWrapper div,
    .servicesContainer div {
        margin-bottom: 2rem;
        float: left;
    }
    .viewAllProjectsButton {
        margin: 1rem auto;
        float: none;
        position: relative;
        /* left: 45%; */
        right: auto;
    }
    .services h1,
    .services h4 {
        text-align: center;
    }
}
@media only screen and (max-width: 1340px) {
    hr::before {
        font-size: .2em;
        top: -0.01rem;
    }
    .contactFormContainer {
        flex-direction: column;
    }
    .contactsIcons {
        flex-direction: row;
        position: unset;
        right: 0;
        left: 0;
    }
    .contactsIcons img {
        margin: 1rem;
    }
}
@media only screen and (max-width: 1070px) {
    .backgroundSquere {
        width: 80%;
    }
    .logoContainer img {
        margin-right: -2.7rem;
    }
    .backgroundSquere .myImage img {
        right: -6rem !important;
    }
    .myContactAndCVcontainer {
        margin: 0 -23.3rem -9.5rem 0 !important;
    }
    .myContactAndCV {
        bottom: -1rem !important;
    }
    .upToTopArrow {
        top: 95%;
    }
    .bodyContainer section {
        padding: 8rem 2rem !important;
        height: max-content;
    }
    section.banner {
        padding: 0 !important;
    }
    .navigationButtonAndContent button::before {
        top: -2.5rem;
    }
    .centerBanner {
        padding-top: 40rem;
    }
}
@media only screen and (max-width: 985px) {
    section.banner {
        height: 50vh !important;
    }
    .aboutMeContainer h1 {
        text-align: center;
    }
    .navigationButtonAndContent button::before {
        top: -2.2rem;
        font-size: .6em;
    }
    .clockWrapper {
        float: right;
        margin-left: 2rem;
    }
    section.reviews {
        padding-top: 40%;
    }
    .contact h1 {
        text-align: center;
    }
    .contactContainer {
        align-items: center;
    }
    .contactMeIcon {
        margin: 0;
    }
}
@media only screen and (max-width: 430px) {
    body {
        background-color: #87805E;
    }
    hr::before {
        top: -0.23rem;
    }
}

/* <!-- 🌐 SCROLL REVEAL --> */
.revealSections {
    position: relative;
    opacity: 0;
    transform: translateY(100px);
    transition: all 1s ease;
}
.activeReveal {
    transform: translateY(0px);
    opacity: 1;
}


