.skills {
    height: max-content !important;
    margin-bottom: 4rem;
}
.skillsContainer h1 {
    text-align: center;
}
.skillsContainer h1 img {
    width: 4rem;
}
.skillsContentWrapper {
    border: 10px solid #B09B71;
    display: flex; 
    justify-content: space-between;
    flex-direction: column;
    width: 87%;
    border-radius: 8px;
    margin: 0 auto;
    padding: 2rem 4rem 2rem 0rem;
}
.skillsContentWrapper div {
    display: flex; 
    justify-content: space-around;
    flex-direction: row;
}
.skillsContainer span {
    color: #87805E;
    border-radius: 8px;
    padding: 2rem;
    margin-bottom: 1.5rem;
    position: relative;
}
.firstLineOfSkills span {
    position: relative;
    -webkit-animation: floatingTexts1 3s linear alternate infinite;
    animation: floatingTexts1 3s linear alternate infinite;
}
.thirdLineOfSkills span  {
    position: relative;
    -webkit-animation: floatingTexts1 3s linear 2s alternate infinite;
    animation: floatingTexts1 3s linear 2s alternate infinite;
}
@keyframes floatingTexts1 {
    from {
        -webkit-transform: translateX(0rem);
        transform: translateX(0rem);
    }
    to {
        -webkit-transform: translateX(4rem);
        transform: translateX(4rem);
    }
}
.secondLineOfSkills span {
    position: relative;
    -webkit-animation: floatingTexts2 3s linear 4s alternate-reverse infinite;
    animation: floatingTexts2 3s linear 4s alternate-reverse infinite;
}
@keyframes floatingTexts2 {
    from {
        -webkit-transform: translateX(4rem);
        transform: translateX(4rem);
    }
    to {
        -webkit-transform: translateX(0rem);
        transform: translateX(0rem);
    }
}
