.exoerienceClockContainer {
    display: flex;
    justify-content: space-between;
}
.clockWrapper {
    float: right;
    margin-left: 7rem;
}
.clockContainer {
    width: 14rem;
    height: 14rem;
    float: right;
    background-color: #87805E;
    border-radius: 50%;
}
.clockInside {
    width: 12rem;
    height: 12rem;
    background-color: #fff;
    background-image:
            radial-gradient(circle at center, #fff 50%, transparent 50%, transparent 65%, #fff 65%),
            linear-gradient(to right, transparent 50%, #87805E 50%, transparent 51%),
            linear-gradient(to bottom, transparent 50%, #87805E 50%, transparent 51%),
            linear-gradient(-30deg, transparent 50%, #87805E 50%, transparent 51%),
                linear-gradient(30deg, transparent 50%, #87805E 50%, transparent 51%),
            linear-gradient(60deg, transparent 50%, #87805E 50%, transparent 51%),
            linear-gradient(-60deg, transparent 50%, #87805E 50%, transparent 51%);
    margin: 2rem 0 4rem;
    border-radius: 50%;
    position: relative;
}
.clockInside::after {
    content: 'GH';
    position: absolute;
    color: #B09B71;
    text-decoration: underline;
    font-size: .8em;
    font-weight: lighter;
    bottom: 0;
    top: 42%;
    right: 0;
    left: 40%;
    margin: auto;
}
.hour {
    width: .2rem;
    height: 2.4rem;
    margin: auto;
    top: -19%;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #19031a;
    position: absolute;
    border-radius: 1rem;
    -webkit-transform: rotateZ(0deg);
    transform: rotateZ(0deg);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    z-index: 1;
}
.minute {
    top: -28%;
    right: 0;
    bottom: 0;
    left: 0;
    width: .2rem;
    height: 3.4rem;
    margin: auto;
    background-color: #87805E;
    position: absolute;
    border-radius: 1rem;
    -webkit-transform: rotateZ(90deg);
    transform: rotateZ(90deg);
    z-index: 1;
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
}
.second {
    top: -36%;
    right: 0;
    left: 0;
    bottom: 0;
    width: .01rem;
    height: 4.2rem;
    margin: auto;
    border-radius: 1rem;
    background-color: #B09B71;
    position: absolute;
    -webkit-transform: rotateZ(180deg);
    transform: rotateZ(180deg);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    position: absolute;
    z-index: 1;
}
