.contactsAndIconsContainer {
    display: flex;
    align-items: center;
}
.contact h1 {
    text-align: right;
}
.contactMeIcon {
    margin-top: -5rem;
    margin-left: 5rem;
}
.contactMeIcon img {
    width: 10rem;
}
.contactMeIcon p {
    font-family: 'Indie Flower', cursive;
}
.contactContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.contactFormContainer {
    display: flex;
    align-items: center;
    width: 45%;
    justify-content: space-between;
    flex-direction: row;
}
.contactsIcons {
    display: flex;
    flex-direction: column;
}
.contactsIcons img {
    width: 3rem;
    margin-bottom: .5rem;
}
.contactMeWrapper input {
    height: 2rem;
    width: 17rem;
    border-radius: 8px;
    border: #87805E 4px solid;
    background-color: #FFF9D7;
}
.contactMeWrapper label {
    color: #87805E;
    padding: .2rem;
    font-size: .8em;
    position: absolute;
    -webkit-transform: translateY(-.3rem) translateX(0rem);
    transform: translateY(-.3rem) translateX(0rem);
    background-color: #fff9d700;
}
.contactMeWrapper textarea {
    resize: none;
    min-width: 267px;
    max-width: 267px;
    min-height:150px;
    max-height:150px;
    border: #87805E 4px solid;
    background-color: #FFF9D7;
    border-radius: 8px;
    outline: none;
}
/*  scroll width */
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #87805E;
}
::-webkit-scrollbar-thumb {
    background: #B09B71; 
    border-radius: 2px;
}
::-webkit-scrollbar-thumb:hover {
    background: #000000; 
}
.contactMeWrapper button.sendMessageButton {
    background-color: #87805E;
    cursor: pointer;
    align-self: center;
    margin: .4rem 0 1rem 0;
    color: #FFF9D7;
    height: 3rem;
    font-size: 1em;
    border-radius: 8px;
    padding: .3rem;
    outline: none;
    border: none;
}
.contactMeWrapper button.sendMessageButton:hover {
    background-color: #B09B71;
    color: #676248;
}
body.dark .contactMeWrapper button.sendMessageButton {
    margin: 1.4rem 0 2rem 5% !important;
    background-color: #1A4D2E;
    color: #AFAEAE;
}
body.dark .contactMeWrapper button.sendMessageButton:hover {
    background-color: #AFAEAE;
    color: #1A4D2E;
}
.contactMeWrapper p.responseForUsersSubmit {
    color: #AFAEAE;
}