/* <!-- 🌐 PRELOADER --> */
.preloader {
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    display: flex;
    justify-content: center;
}
.preloader .loading {
    background-color: rgba(165, 42, 42, 0);
    width: 1.5rem;
    height: 1.5rem;
    position: relative;
    margin: 0 auto;
    top: 45%;
    bottom: 0;
    border-radius: 5%;
    animation: rotatepreloader 7s ease-in-out alternate infinite;
}
@keyframes rotatepreloader {
    from {
        box-shadow: #87805E 0px -20px 0px 0px, 
                    #382933 -20px 0px 0px 0px, 
                    #382933 20px 0px 0px 0px, 
                    #87805E 0px 20px 0px 0px;
    }
    40%{
        box-shadow: #87805E -20px 0px 0px 0px, 
                    #382933 0px -20px 0px 0px, 
                    #382933 0px 20px 0px 0px, 
                    #87805E 20px 0px 0px 0px;
    }
    60% {
        box-shadow: #87805E 0px -20px 0px 0px, 
                    #382933 -20px 0px 0px 0px, 
                    #382933 20px 0px 0px 0px, 
                    #87805E 0px 20px 0px 0px;
        transform: rotate(0deg);
    }
    80% {
        box-shadow: #87805E -40px 0px 0px -5px, 
                    #382933 0px -40px 0px -5px, 
                    #382933 0px 40px 0px -5px, 
                    #87805E 40px 0px 0px -5px;
        transform: rotate(20deg);
    }
    to {
        box-shadow: #87805E -40px 0px 0px -10px, 
                    #382933 0px -40px 0px -10px, 
                    #382933 0px 40px 0px -10px, 
                    #87805E 40px 0px 0px -10px;
        transform: rotate(-20deg);
    }
}
.preloader.hidden {
    animation: fadeOut 1s;
    animation-fill-mode: forwards;
}

@keyframes fadeOut {
    100% {
        opacity: 0;
        display: none;
        visibility: hidden;
    }
}