.reviewsContainer {
    box-shadow: #B09B71 0px 0px 11px 0px;
    border-radius: 8px;
    padding: 1.2rem 1rem;
    display: flex;
    align-items: center;
    position: relative;
}
.reviewsContainer button {
    text-align: center;
    font-size: 1.2em;
    width: 4rem;
    color: #FFF9D7;
    background-color: #B09B71;
    border-radius: 50%;
    padding: 1rem;
    height: max-content;
    cursor: pointer;
    outline: none !important;
}
.reviewsContainer button:hover {
    color: #87805E;
    background-color: #FFF9D7;
    border: #87805E 2px solid;
}
body.dark .reviewsContainer button {
   background-color: #382933; 
   color: #AFAEAE;
}
body.dark .reviewsContainer button:hover {
    border: #AFAEAE 2px solid;
}
.reviewsWrapper {
    width: 70%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.reviewsWrapper div {
    padding: .8rem;
    width: 100%;
    margin-right: .8rem;
    background-color: #B09B71;
}
.reviewsWrapper div:last-child {
    margin: 0;
}
.reviewersIcon img {
    margin-left: -4.5rem;
    width: 6rem;
}
.reviewersIcon span {
    color: #FFF9D7;
    text-align: right;
    padding-top: 3rem;
    font-family: Arial, Helvetica, sans-serif;
    float: right;
    font-size: 4em;
}
.reviewsWrapper div p {
    text-align: justify;
}
.reviewsWrapper div h5 {
    color: #87805E;
}
ul.reviewsLength {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: -.1rem;
}
ul.reviewsLength li {
    color: #87805E;
    font-size: .5em;
    width: min-content;
    margin-top: -1.1rem;
    margin-right: .5rem;
}

ul.reviewsLength li.currentDot {
    color: #B09B71;
}