section.banner {
    padding: 1rem 4rem 4rem;
    height: 100vh;
}
.banner  {
    position: relative;
}
.backgroundSquere {
    z-index: -1;
    top: 0;
    left: 0;
    width: 90%;
    height: 23rem;
    background-color: #87805E;
    background-image: url('/src/images/backgroundImages/lines.png');
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin: -1rem -4rem;
}
.backgroundSquere .myImage img {
    position: relative;
    right: -10rem;
    bottom: -4rem;
    width: 300px;
    height: 280px;
}
.myContactAndCVcontainer {
    display: flex;
    flex-direction: column;
    margin: 0 -25.3rem -9.5rem 0;
    z-index: 9 !important;
    text-align: center;
}
body.chinese .myContactAndCVcontainer {
    margin: 0 -22rem -9.7rem 0;
}
.myContactAndCVcontainer a {
    background-color: #FFF9D7;
    border: 4px solid #87805E;
    color: #87805E;
    border-radius: 4rem;
    margin-top: .4rem;
    width: max-content;
    padding: .5rem;
    font-size: .6em;
    box-shadow: #19031a 0px 0px 0px 2px;
}
.myContactAndCVcontainer a:first-child {
    background-color: #87805E;
    border: 4px solid #87805E;
    color: #FFF9D7;
}
.myContactAndCVcontainer a:hover {
    background-color: #87805E;
    border: 4px solid #B09B71;
    color: #B09B71;
}
.myContactAndCVcontainer a:first-child:hover {
    background-color: #B09B71 ;
    border: 4px solid #87805E;
    color: #87805E;
}
.navigationBarContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    padding: 1rem 3rem;
}
button {
    outline: none;
}
.navigationButtonAndContent button {
    font-size: 2em;
    cursor: pointer;
    width: 1.5rem;
    height: 1.5rem;
    display: table;
    clear: both;
    color: #FFF9D7;
    background-color: #87805E;
    border: none;
    outline: none;
    position: relative;
    margin-top: 2rem;
}
.navigationButtonAndContent button::before {
    content: '▤';
    text-shadow: #000 0px 0px 4px;
    top: -2rem;
    font-size: .7em;
    left: -.3rem;
    width: 40px;
    height: 35px;
    position: relative;
    transition: content 5s;
}
.menuClicked .navigationButtonAndContent button::before {
    content: "▩";
}
.navigationWrapper .navigationContentWrapper ul {
    background-color: #B09B7141;
    margin-top: .7rem;
    border-radius: 8px;
    padding: 1rem;
    position: absolute;
    z-index: 97;
}
.navigationWrapper .navigationContentWrapper ul li {
    color: #FFF9D7;
    border: #87805E 3px solid;
    background-color: #B09B71;
    font-size: .6em;
    border-radius: 8px;
    padding: .5rem;
    margin-bottom: .5rem;
}
.navigationWrapper .navigationContentWrapper ul li a {
    color: #FFF9D7;
}
.navigationWrapper .navigationContentWrapper ul li:hover {
    color: #87805E;
    border: #B09B71 3px solid;
    background-color: #87805E;
}
.navigationWrapper .navigationContentWrapper ul li:last-child {
    margin: 0;
}
.navigationWrapper .navigationContentWrapper ul {
    display: none;
}
.menuClicked .navigationContentWrapper ul {
    display: block;
}
.navigationWrapper .navigationContentWrapper ul li img {
    width: .8rem;
    margin-right: .5rem;
}
.navigationWrapper {
    display: flex;
}
/* theme and language */
.lightDarkAndLanguages {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.lightDarkAndLanguages div {
    margin: 0 1rem;
}
/* ----hide inputs */
.switchButton input, 
.changeLanguages input { 
    opacity: 0;
    width: 0;
    height: 0;
}
/* Theme */
/* ----set Background */
.darkLight {
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    -webkit-transition: .4s;
    transition: .4s;
    height: 1.39rem;
    width: fit-content;
}
/* ----set button */
.darkLight:before {
    content: "◑";
    text-shadow: #000 0px 0px 4px;
    font-size: 1.5em;
    color: #FFF9D7;
    font-weight: light;
    height: 1.3rem;
    left: 0rem;
    padding: .1rem 0 0 .1rem;
    bottom: 0;
    -webkit-transition: .4s;
    transition: all .7s;
}
/* ----button animation */
input:checked + .darkLight::before {
    content: "◐";
    height: fit-content;
    color: #AFAEAE;
    height: 1.3rem;
    border-right: none;
    transition: all .7s;
}
input:checked + .darkLight::after {
    color: gray;
}
/* LANGUAGES */
.lightDarkAndLanguages .changeLanguages {
    height: min-content;
    font-size: .7em;
}
/* ----set Background */
.chineseEnglish {
    cursor: pointer;
    top: 0;
    left: 4rem;
    right: 0;
    bottom: 0;
    color: #FFF9D7;
    height: min-content;
}
/* ---set button */
.chineseEnglish::before {
    content: "中文";
    text-shadow: #000 0px 0px 4px;
    height: fit-content;
    text-align: center;
    width: 2px;
    font-size: 1.4em;
    top: 0;
    -webkit-transition: .4s;
    transition: .4s;
}
input:checked + .chineseEnglish::before {
    content: "EN";
    height: fit-content;
    width: 2px;
}

.logoContainer img {
    width: 7rem;
}

.myContactAndCV {
    position: absolute;
    bottom: 2rem;
    left: 0;
    display: flex;
    width: fit-content;
    height: fit-content;
    justify-content: space-between;
    align-items: flex-end;
    padding: 2rem;
}
.explanationTitle h1 {
    color: #19031a;
    padding: 0;
}
.explanationTitle p {
    width: fit-content;
    color: #19031a;
}
hr {
    height: .8rem;
    color: #87805E;
    width: 97%;
    border: none;
    border-radius: 8px;
    margin: auto;
    background-color: #87805E;
    position: relative;
}